import uniLoadMore from "@/components/uni-load-more.vue"
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			tabBars: [
				{name:'全部',id:0},
				{name:'待分享',id:1},
				{name:'待预约',id:2},
				{name:'已预约',id:3},
				{name:'待评价',id:4},
			],
			scrollLeft: 0,
			tabIndex: 0,
			symbolId: 0,//分类id
			lists:[],
			loadingType: 0,
			next : -1,
			start: 0,
			size: 15,
			record: false,
			bottom: false,
			phone:''
		};
	},
	onLoad: function(e) {
		uni.setStorageSync('biId','')
		this.tabIndex = e.tabIndex
		this.symbolId = e.tabIndex
		this.authKey = uni.getStorageSync('authKey');
	},
	onShow() {
		this.start = 0
		this.requestorderList()
		this.phonea();
	},
	methods:{
		// 电话预约电话
		phonea(){
			uni.request({
				url: this.http+'appointment/getClubContact',
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					console.log(res.data)
					if (res.data.code == 200) {
						this.phone = res.data.data
					}
				},
			});
		},
		// 店里电话
		phonenumber(){
			wx.makePhoneCall({
		        phoneNumber: this.phone
		    });
		},
		// 技师电话
		contant(e){
			wx.makePhoneCall({
			    phoneNumber: e
			});
		},
		getElSize(id) { //得到元素的size
		// console.log(id)
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select("#" + id).fields({
					size: true,
					scrollOffset: true
				}, (data) => {
					res(data)
				}).exec()
			})
		},
		async tapTab(e) { //点击tab-bar
			this.symbolId = e.currentTarget.id
			// console.log(this.symbolId)
			if (this.tabIndex === e.target.dataset.current) {
				return false;
			} else {
				let tabBar = await this.getElSize("tab-bar"),
				tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
				this.scrollLeft = tabBarScrollLeft;
				this.isClickChange = true;
				this.tabIndex = e.target.dataset.current;
			}
			this.start = 0
			this.requestorderList()
		},
		// 预约
		appointment(e){
			uni.navigateTo({
				url: '/pages/reservationService/reservationService?id=' + e
			});
		},
	    // 请求订单的列表
		requestorderList: function(isLoading = true) {
			if (isLoading)	uni.showLoading({title: '加载中'})
			var that = this;
			let querys = "?start=" + this.start + "&size=" + this.size 
			uni.request({
				url: this.http+'order/list' + querys,
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {
					type:that.symbolId
				},
				success: res => {
					let data = res.data.data
					// console.log(res.data)
					if (res.data.code == 200) {
						// that.lists = res.data.data.list
						let order = data.list
						
						if (that.start == 0) 
							that.lists = []
					   
						order.forEach(function(item){
							item.product.cover = that.static + item.product.cover
						})
	                    that.start = data.next	
						that.lists = that.lists.concat(order)
						if (order.length == 0) {
							that.loadingType = 2
						}
						else if (order.length < that.size || that.start == -1)
							that.loadingType = 2
						else 
							that.loadingType = 0
						if (that.lists.length == 0) 
							that.record = true
						else
							that.record = false
					}
				},
				fail: () => {
					console.log("请求失败")
				},
				complete: () => {
					if(isLoading) uni.hideLoading()
				}
			});
		},
		invite(e){
			 uni.navigateTo({
				url: '/pages/orderDetail/orderDetail-not?id=' + e
			});
		},
		detailS(e,a){
			if(a == 0){
				uni.navigateTo({
					url: '/pages/orderDetail/orderDetail?id=' + e
				});
			}else{
				uni.navigateTo({
					url: '/pages/orderDetail/orderDetail-success?id=' + e
				});
			}
		},
		evelate(e){
			uni.navigateTo({
				url: '/pages/mine/evaluate/evaluate?id=' + e
			});
		},
		lower(){
			if (this.loadingType == 2) {
				return
			}
			this.requestorderList(false)
		}
	}
}
